import React, { useState, useEffect, history } from 'react';
import Header from './Header';
import Footer from './Footer';
import { useParams } from 'react-router-dom';
import axios from 'axios'
import { Link } from 'react-router-dom';
import jwt_decode from 'jwt-decode';


export default function ProductDetails() {



	const { pid } = useParams();
	console.log(pid)

	const [products, setProducts] = useState([]);

	useEffect(() => {
		async function getCategoryData() {
			const response = await axios
				.get(`http://ecommerce-poc-api.nastechltd.co/api/products/${pid}`)
				.then(
					(response) => {
						console.log(response.data);
						setProducts(response.data);
					},
					(error) => {
                        console.log(error)
					}
				);
		}

		getCategoryData();
	}, []);


	const cart_id = localStorage.getItem('cart_id')
	const url = localStorage.getItem('url')


	// const [cart, setCart] = useState();
	const [quantity, setQuantity] = useState(0);
	// Create handleIncrement event handler
	const handleIncrement = () => {
		if(quantity < products.quantity){
			setQuantity(prevCount => prevCount + 1);
		}else{
			alert('Availble Quantity crossed')
		}
	  };
	
	  //Create handleDecrement event handler
	  const handleDecrement = () => {
		  if(quantity>0){
			  setQuantity(prevCount => prevCount - 1);
		  }else{
			  alert('Please select one Quantity')
		  }
	  };

	const addtocart = () => {
		const response = axios
			.post(`http://ecommerce-poc-api.nastechltd.co/api/cart_product`, {
				quantity: quantity,
				unit_price: products.price,
				cart_id: cart_id,
				products_id: products.id,
				image: products.image
			})
			.then(
				(response) => {
					console.log(response.data);
					alert('Product added to cart !')
					// setCategory(response.data);
				},
				(error) => {
					console.log(error)
				}
			);

	}

	const [category, setCategory] = useState([]);

    useEffect(() => {
        async function getCategoryData() {
            const response = await axios
                .post(`http://ecommerce-poc-api.nastechltd.co/api/show_store`,{
                    store_name:url
                })
                .then(
                    (response) => {
                        // Buffer.from(response.data, 'binary').toString('base64');
                        console.log(response.data);
                        setCategory(response.data);
                    },
                    (error) => {
                        console.log(error)
                    }
                );
        }

        getCategoryData();
    }, []);



	return (
		<>
			<Header />

			<section>
				<div class="container">
					<div class="row">
						<div class="col-sm-3">
							<div class="left-sidebar">
								<h2>Category</h2>
								<div class="panel-group category-products" id="accordian">
									{category.map((val, id) => {
										return (
											<>
												<div className="panel panel-default">
													<div className="panel-heading">
														<h4 className="panel-title"><Link to={`/categoryproduct/${val.id}`}>{val.name}</Link></h4>
													</div>
												</div>

											</>
										);
									})}
								</div>
							</div>
						</div>

						<div class="col-sm-9 padding-right">
							<div class="product-details">
								<div class="col-sm-5">
									<div class="view-product">
										<img src={products.image} alt="" />
									</div>
								</div>
								<div class="col-sm-7">
									<div class="product-information">
										<img src="images/product-details/new.jpg" class="newarrival" alt="" />
										<h2>{products.name}</h2>
										<p>Category ID: {products.categories_id}</p>
										<img src="images/product-details/rating.png" alt="" />
										<span>
											<span>US ${products.price}</span>
											<label>Available Quantity:</label>
											<input type="text" value={products.quantity} />
											<br />
											<br />
											<label>Required Quantity:</label>
											<div className="cart_quantity_button">
												<a className="cart_quantity_up" onClick={handleIncrement} style={{cursor:'pointer'}}> + </a>
												<input className="cart_quantity_input" disabled onChange={(e) => setQuantity(e.target.value)} value={quantity}/>
												<a className="cart_quantity_down" onClick={handleDecrement} style={{cursor:'pointer'}}> - </a>
											</div>
											<br />
											<br />
											<button onClick={() => addtocart()} className="btn btn-default cart"><i className="fa fa-shopping-cart"></i>Add to cart</button>

										</span>
										<p><b>Availability:</b> In Stock</p>
										<p><b>Condition:</b> New</p>
										<p><b>Brand:</b> E-SHOPPER</p>
										<a href=""><img src="images/product-details/share.png" class="share img-responsive" alt="" /></a>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>

			<Footer />
		</>
	)
}