import React, { useState, useEffect } from 'react';
// import {history} from 'react-router-dom';
import axios from 'axios';
import Header from './Header';
import Footer from './Footer';
import Contact from './Contact';
import jwt_decode from 'jwt-decode';
import { Link, useHistory } from 'react-router-dom'


export default function Cart() {

	let history = useHistory();
	const cart_id = localStorage.getItem('cart_id')
	const [prd, setPrd] = useState([])

	useEffect(() => {
		function getCategoryData() {
			const { data: response } = axios.post(
				`http://ecommerce-poc-api.nastechltd.co/api/show_cart`,{
					cart_id:cart_id
				}).then((response) => {
					console.log(response);
					setPrd(response.data)
				}, (error) => {
					console.log(error)
				});
		}

		getCategoryData();

	}, []);


	const [number, setNumber] = useState("");
	const [address, setAddress] = useState("");

	function SendLoginDetails(e) {
		var token = localStorage.getItem("customer_token");
		var decoded = jwt_decode(token);

		e.preventDefault();
		if (
			number === "" ||
			address === ""
		) {
			alert("Please fill all input feilds!");
		}
		else {
			var url = localStorage.getItem('url')

			const { data: res } = axios.post(
				`http://ecommerce-poc-api.nastechltd.co/api/order`,
				{
					contact: number,
					delivery_address: address,
					payment_method: 'COD',
					customer_id: decoded.sub,
					store_name: "http://grocery-poc.nastechltd.co/"
				}
			).then((response) => {
				console.log(response);
				alert('Your order in progress ')
				const { data: res } = axios.post(
					`http://ecommerce-poc-api.nastechltd.co/api/order_item`, {
					customer_id: decoded.sub
				}).then((res) => {
					console.log(res);
				}, (error) => {
					alert('Email already taken!')
				});
				history.push("/");
			}, (error) => {
				alert('Email already taken!')
			});


		}
	}

	function DeleteProduct(e) {
		const { data: response } = axios
		  .delete(
			`http://ecommerce-poc-api.nastechltd.co/api/cart_product/${e.target.value}`
		  )
		  .then(
			(response) => {
			  console.log(response);
			  window.location.reload(false);
			},
			(error) => {
console.log(error)			}
		  );
	  }

	return (
		<>
			<Header />
			<section id="cart_items">
				<div className="container">
					<div className="breadcrumbs">
						<ol className="breadcrumb">
							<li><a href="#">Home</a></li>
							<li className="active">Shopping Cart</li>
						</ol>
					</div>
					<div className="table-responsive cart_info">
						<table className="table table-condensed">
							<thead>
								<tr className="cart_menu">
									<td className="image">Item</td>
									<td className="description"></td>
									<td className="price">Price</td>
									<td className="quantity">Quantity</td>
									<td className="total">Total</td>
									<td></td>
								</tr>
							</thead>
							<tbody>
								{
									prd.map((val, id) => {
										return (
											<>
												<tr>
													<td className="cart_product">
														<a href=""><img src={val.image} alt="" style={{ width: '50px' }} /></a>
													</td>
													<td className="cart_description">
														<h4><a href="">{val.name}</a></h4>
														<p>Web ID: 1089772</p>
													</td>
													<td className="cart_price">
														<p>${val.unit_price}</p>
													</td>
													<td className="cart_quantity ">
														<div className="cart_quantity_button">
															<input className="cart_quantity_input" type="text" disabled value={val.quantity} />
														</div>
													</td>
													<td className="cart_total">
														<p className="cart_total_price">${val.total}</p>
													</td>
													<td className="cart_delete">
														<button className="cart_quantity_delete"  value={val.id} onClick={DeleteProduct}>Remove Product</button>
													</td>
												</tr>

											</>
										)

									}
									)
								}

							</tbody>
						</table>
					</div>
				</div>
			</section>

			<section id="do_action">
				<div className="container">
					<div className="heading">
						<h3>What would you like to do next?</h3>
						<p>Proceed to check out and fill up all the details for delivery!</p>
					</div>
					<div className="row">
						<div className="col-sm-12">
							<div className="chose_area">
								<ul className="user_info">
									<label>Contact Number:</label>
									<input type="text" placeholder="Enter Phone Number" onChange={(e) => setNumber(e.target.value)} />
									<br />
									<br />
									<label>Payment Method:</label>
									<input type="text" value="Cash on Delivery (COD)" disabled />
									<br />
									<br />
									<label>Address:</label>
									<textarea type="text" placeholder="Enter Delivery Address" rows="5" onChange={(e) => setAddress(e.target.value)}>

									</textarea>

								</ul>
								<ul className="user_info">
									<button className="btn btn-default check_out " href="" style={{ float: 'right' }} onClick={SendLoginDetails}>Continue to Checkout</button>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</section>

			<Footer />
		</>
	)
}