import React, { useState } from "react";
import { Link, useHistory } from 'react-router-dom'
import mainbg from '../images/mainbg.jpg'
import loginside from '../images/loginside.jpg'
import axios from 'axios';
import Logo from '../images/home/logo.png';
import TextField from "@material-ui/core/TextField";
import Radio, { RadioProps } from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import { useLocation } from 'react-router-dom';
import jwt_decode from 'jwt-decode';



export default function Login() {
    let history = useHistory();
    const location = useLocation()

  



    //     const url = window.location.href
    //    console.log(url )  


    const url = localStorage.getItem('url');

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    // let LoggedIn = false;

    function LoginBtn(e) {
        e.preventDefault();
        const { data: response } = axios.post(`http://ecommerce-poc-api.nastechltd.co/api/login`, {
            email: email,
            password: password,
        })
            .then((response) => {
                localStorage.setItem('customer_token', response.data);
                var token = localStorage.getItem("customer_token");
                var decoded = jwt_decode(token);
                if (decoded.role === 'customer') {
                    console.log(response.data);
                    function getCartData() {
                        var token = localStorage.getItem("customer_token");
                        var decoded = jwt_decode(token);
                        const response =  axios
                            .post(`http://ecommerce-poc-api.nastechltd.co/api/cart`,{
                                customer_id:decoded.sub
                            })
                            .then(
                                (response) => {
                                    console.log(response.data.id);
                                    localStorage.setItem('cart_id',response.data.id);
                                },
                                (error) => {
                                    console.log(error)
                                }
                            );
                    }
            
                    getCartData();
                    history.push('/');
                } else {
                    alert('Wrong email or password!')
                }
            }, (error) => {
                console.log(error);
                history.push('/login');
                alert('Wrong emai or password!')
            });
    }





    const [fullname, setFullname] = useState("");
    const [useremail, setUserEmail] = useState("");
    const [usergender, setUserGender] = useState("");
    const [userpassword, setUserPassword] = useState("");
    const [usercpassword, setUserCpassword] = useState("");

    function SendLoginDetails(e) {
        e.preventDefault();
        if (userpassword !== usercpassword) {
            alert("Password incorrect!");
        } else if (
            fullname === "" ||
            useremail === "" ||
            usergender === "" ||
            userpassword === "" ||
            usercpassword === ""
        ) {
            alert("Please fill all input feilds!");
        } else {

            const { data: response } = axios.post(
                `http://ecommerce-poc-api.nastechltd.co/api/register_customer`,
                {
                    full_name: fullname,
                    email: useremail,
                    password: userpassword,
                    gender: usergender,
                    store: url
                }).then((response) => {
                    console.log(response);
                    localStorage.setItem('customer_token', response.data);
                    var token = localStorage.getItem("customer_token");
                    var decoded = jwt_decode(token);
                    if (decoded.role === 'customer') {
                        console.log(response.data);
                        function getCartData() {
                            var token = localStorage.getItem("customer_token");
                            var decoded = jwt_decode(token);
                            const response =  axios
                                .post(`http://ecommerce-poc-api.nastechltd.co/api/cart`,{
                                    customer_id:decoded.sub
                                })
                                .then(
                                    (response) => {
                                        console.log(response.data.id);
                                        localStorage.setItem('cart_id',response.data.id);
                                    },
                                    (error) => {
                                        console.log(error)
                                    }
                                );
                        }
                
                        getCartData();
                    }                    
                    history.push("/");
                }, (error) => {
                    alert('Email already taken!')
                    history.push('/login');
                });
        }
    }




    return (
        <>
            <header id="header">
                <div className="header-middle">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-4">
                                <div className="logo pull-left">
                                    <Link to="/"><img src={Logo} alt="" /></Link>
                                </div>
                            </div>
                            <div className="col-sm-8">
                                <div className="shop-menu pull-right">
                                    <ul className="nav navbar-nav">
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </header>


            <section id="form">
                <div class="container">
                    <div class="row">
                        <div class="col-sm-4 col-sm-offset-1">
                            <div class="login-form">
                                <h2>Login to your account</h2>
                                <form >
                                    <input onChange={(e) => setEmail(e.target.value)} type="text" name="email" placeholder="Enter a valid email address" />
                                    <input type="password" onChange={(e) => setPassword(e.target.value)} name="password" placeholder="Enter password" />
                                    <span>
                                        <input type="checkbox" class="checkbox" />
								Keep me signed in
							</span>
                                    <button type="submit" class="btn btn-default" onClick={LoginBtn}>Login</button>
                                </form>
                            </div>
                        </div>
                        <div class="col-sm-1">
                            <h2 class="or">OR</h2>
                        </div>
                        <div class="col-sm-4">
                            <div class="signup-form">
                                <h2>New User Signup!</h2>
                                <form >
                                    <input onChange={(e) => setFullname(e.target.value)} name="fulname" placeholder="Full Name" />
                                    <input type="email" onChange={(e) => setUserEmail(e.target.value)} name="fulname" placeholder="Email Address" />
                                    <div className="row  ">
                                        <label className="">
                                            <h6 className=" ">Gender</h6>
                                        </label>
                                        <br /> <br />
                                        <RadioGroup className="d-inline ">
                                            <span className="pl-">
                                                <FormControlLabel value="male" control={<Radio />} label="Male"
                                                    onChange={(e) => setUserGender(e.target.value)}
                                                />
                                            </span>
                                            <span className="">
                                                <FormControlLabel value="female" control={<Radio />} label="Female"
                                                    onChange={(e) => setUserGender(e.target.value)}
                                                />
                                            </span>
                                        </RadioGroup>
                                    </div>
                                    <input type="password" onChange={(e) => setUserPassword(e.target.value)} placeholder="Password" />
                                    <input type="password" onChange={(e) => setUserCpassword(e.target.value)} placeholder="Confirm Password" />
                                    <button class="btn btn-default" onClick={SendLoginDetails}>Signup</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </>
    );
}

