import React, { useState, useEffect, history  } from 'react';
import Header from './Header';
import Footer from './Footer';
import axios from 'axios';
import {useParams} from 'react-router-dom'
import { Link } from 'react-router-dom';

export default function CategoryProduct() {


    const [add,setAdd] = useState('');

    function Additem(){
        setAdd()
    }

    const {cpid} = useParams()
    console.log(cpid)

    const [products, setProducts] = useState([]);

    useEffect(() => {
        async function getCategoryData() {
            const response = await axios
                .post(`http://ecommerce-poc-api.nastechltd.co/api/filtering`,
                {
                    categories_id:cpid
                })
                .then(
                    (response) => {
                        console.log(response.data);
                        setProducts(response.data);
                    },
                    (error) => {
                        console.log(error)
                    }
                );
        }

        getCategoryData();
    }, []);


    const [cart , setCart] = useState([]);

    const addtocart = (product) =>{
        // console.log(product)
        localStorage.setItem('products', JSON.stringify(cart))
        setCart([...cart,product])
    }


    const [category, setCategory] = useState([]);

    useEffect(() => {
        async function getCategoryData() {
            const response = await axios
                .get(`http://ecommerce-poc-api.nastechltd.co/api/category`)
                .then(
                    (response) => {
                        // Buffer.from(response.data, 'binary').toString('base64');
                        console.log(response.data);
                        setCategory(response.data);
                    },
                    (error) => {
                        console.log(error)
                    }
                );
        }

        getCategoryData();
    }, []);


    return (
        <>
           <Header />

            <section id="advertisement">
                <div class="container">
                    <img src="images/shop/advertisement.jpg" alt="" />
                </div>
            </section>

            <section>
                <div class="container">
                    <div class="row">
                        <div class="col-sm-3">
                            <div class="left-sidebar">
                                <h2>Category</h2>
                                <div class="panel-group category-products" id="accordian">
                                {category.map((val, id) => {
                                        return (
                                            <>
                                                <div className="panel panel-default">
                                                    <div className="panel-heading">
                                                        <h4 className="panel-title"><Link to={`/`}>{val.name}</Link></h4>
                                                    </div>
                                                </div>

                                            </>
                                        );
                                    })}
                                </div>
                               

                                <div class="price-range">
                                    <h2>Price Range</h2>
                                    <div class="well">
                                        <input type="text" class="span2" value="" data-slider-min="0" data-slider-max="600" data-slider-step="5" data-slider-value="[250,450]" id="sl2" /><br />
                                        <b>$ 0</b> <b class="pull-right">$ 600</b>
                                    </div>
                                </div>

                                <div class="shipping text-center">
                                    <img src="images/home/shipping.jpg" alt="" />
                                </div>

                            </div>
                        </div>

                        <div class="col-sm-9 padding-right">
                            <div class="features_items">
                                <h2 class="title text-center">Features Items</h2>
                                {products.map((val, id) => {
                                    return (
                                        <>
                                        <Link to={`/productdetails/${val.id}`}>
                                <div className="col-sm-4" key={id}>
                                    <div className="product-image-wrapper">
                                        <div className="single-products">
                                            <div className="productinfo text-center">
                                                <img src={val.image} style={{width:'150px',height:'150px'}} alt="" />
                                                <h2>${val.price}</h2>
                                                <p>{val.name}</p>
                                            </div>
                                            <div className="product-overlay">
                                                <div className="overlay-content">
                                                    <h2>${val.price}</h2>
                                                    <p>{val.name}</p>
                                                    <button onClick={()=>addtocart(val)} className="btn btn-default add-to-cart"><i className="fa fa-shopping-cart"></i>Add to cart</button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                        </Link>
                                        </>
                                    );
                                })}

                            </div>
                        </div>
                    </div>
                </div>
            </section>

         
<Footer />
        </>
    )
}